body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  background-color: #62121F;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.scrollTop {
  position: fixed;
  padding: 10px 0;
  top: 0;
  background-color: white;
  border-bottom: 3px solid black;
  width: 100%;
}

div#cover {
  width: auto;
}

img#cover_img,
img#save_the_date {
  width: 100%;
  margin-bottom: -0.2em;
}

.slider_whitespace {
  position: relative;
  background-repeat: no-repeat;
  background-position: left top;
  background-origin: content-box;
  background-attachment: fixed;
  background-color: transparent;
}

.bottom_img-div {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.bottom_img {
  position: fixed;
  top: 0em;
}

.slider_img {
  opacity: 1;
  margin-bottom: -0.2em;
}

.isVisible {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0em;
  width: 100%;
  z-index: 1000;
  background: #00337aa1;
  color: white;
  backdrop-filter: blur(5px);
  height: 2em;
}


div.contador {
  display: grid;
  grid-gap: 0px;
  margin-left: calc(50% - 5em);
  width: 10em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  text-align: center;
  color: #62121F;
}
div.count_days, div.count_hours, div.count_minutes, div.count_secs {
  display: inline;
  width: 2.5em;
}
div.count_days {
  grid-column: 1;
}
div.count_hours {
  grid-column: 2;
}
div.count_minutes {
  grid-column: 3;
}
div.count_secs {
  grid-column: 4;

}

